$day-size: 100px;
$border-width: 1px solid #000;
$gap: 1px;
$padding: 10px;
$color_background: (#eee);
$start-days: ("mon": 0, "tue": 1, "wed": 2,
"thu":3, "fri":4, "sat": 5, "sun":6);
$padding: 10px;
$font-size: 30px;
$week: 7;
$calendar-size: ($day-size * $week) + ($padding * 2) + ($gap * $week);
